@use "sass:map";
@use "../mixins/breakpoint";
@use "../mixins/text";
@use "../globals";

// stylelint-disable declaration-no-important


$alignments: left, center, right, justify !default;


// Text

.text-monospace {
 font-family: globals.$font-family-monospace;
 }

// Alignment

@each $breakpoint in map.keys(globals.$breakpoint-list) {
  @include breakpoint.media-breakpoint-up($breakpoint) {
    $infix: breakpoint.breakpoint-infix($breakpoint, globals.$breakpoint-list);

    @each $value in $alignments {
      .text#{$infix}-#{$value}   {
 text-align: $value !important;
 }
    }
  }
}


// Truncate

.text-truncate {
 @include text.truncate();
 }
.text-nowrap   {
 white-space: nowrap !important;
 }


// Transformation

.text-lowercase  {
 text-transform: lowercase !important;
 }
.text-uppercase  {
 text-transform: uppercase !important;
 }
.text-capitalize {
 text-transform: capitalize !important;
 }


// Weight and italics

.text-weight-light  {
 font-weight: globals.$font-weight-light !important;
 }
.text-weight-normal {
 font-weight: globals.$font-weight-normal !important;
 }
.text-weight-bold,
.text-b,
.text-bold,
.text-strong {
  font-weight: globals.$font-weight-bolder !important;
}

.text-italic,
.text-i,
.text-cite,
.text-em,
.text-var,
.text-dfn {
  font-style: italic !important;
}

.text-s,
.text-strike,
.text-del {
  text-decoration: line-through !important;
}

.text-u,
.text-underline,
.text-ins {
  text-decoration: underline !important;
}


// Sizes

.text-small {
  @include text.font-size(globals.$small-font-size);
}

.text-body {
 color: globals.$theme-text-color !important;
 }
.text-muted {
 color: globals.$text-muted !important;
 }
