@use "./position";

// Element states

// Hover

@mixin hover() {
  @media (any-hover: hover) {
    :host(:hover) {
      @content;
    }
  }
}


// Focus

@mixin focus() {
  :host(:focus),
  :host(.state-focused) {
    @content;
  }
}


// Active

@mixin active() {
  :host(:active),
  :host(.state-activated) {
    @content;
  }
}


// Disabled

@mixin disabled() {
  :host(:disabled),
  :host(.state-disabled) {
    @content;
  }
}


// Button

@mixin button() {
  @include position.set(0, 0, 0, 0);

  position: absolute;

  content: "";

  opacity: 0;
}