@use "../mixins/transition";
@use "../mixins/padding";
@use "../globals";


// Table
// --------------------------------------------------

.table {
  width: 100%;
  max-width: 100%;

  margin-bottom: globals.$spacer;

  tr {
    @include transition.set(background-color .3s ease);
  }

  th,
  td {
    @include padding.set(globals.$table-cell-padding);

    border-top: globals.$table-border-width solid globals.$table-border-color;

    vertical-align: top;
  }

  thead th {
    border-bottom: (2 * globals.$table-border-width) solid globals.$table-border-color;

    vertical-align: bottom;
  }

  tbody + tbody {
    border-top: (2 * globals.$table-border-width) solid globals.$table-border-color;
  }
}


// Border versions
//
// Add or remove borders all around the table and between all the columns.

.table-bordered {
  border: globals.$table-border-width solid globals.$table-border-color;

  th,
  td {
    border: globals.$table-border-width solid globals.$table-border-color;
  }

  thead {
    th,
    td {
      border-bottom-width: (2 * globals.$table-border-width);
    }
  }
}

.table-borderless {
  th,
  td,
  thead th,
  tbody + tbody {
    border: 0;
  }
}

// Zebra-striping
//
// Default zebra-stripe styles (alternating gray and transparent backgrounds)

.table-striped {
  tbody tr:nth-of-type(#{globals.$table-striped-order}) {
    background-color: globals.$table-accent-bg;
  }
}


// Hover effect
//
// Placed here since it has to come after the potential zebra striping

.table-hover {
  tbody tr {
    &:hover {
      background-color: globals.$table-hover-bg;
    }
  }
}
