@use "sass:map";
@use "../mixins/breakpoint";
@use "../globals";

// stylelint-disable declaration-no-important

// Margin and Padding

@each $breakpoint in map.keys(globals.$breakpoint-list) {
  @include breakpoint.media-breakpoint-up($breakpoint) {
    $infix: breakpoint.breakpoint-infix($breakpoint, globals.$breakpoint-list);

    @each $prop, $abbrev in (margin: m, padding: p) {
      @each $size, $length in globals.$spacers {

        .#{$abbrev}#{$infix}-#{$size} {
 #{$prop}: $length !important;
 }
        .#{$abbrev}t#{$infix}-#{$size},
        .#{$abbrev}y#{$infix}-#{$size} {
          #{$prop}-top: $length !important;
        }
        .#{$abbrev}r#{$infix}-#{$size},
        .#{$abbrev}x#{$infix}-#{$size} {
          #{$prop}-right: $length !important;
        }
        .#{$abbrev}b#{$infix}-#{$size},
        .#{$abbrev}y#{$infix}-#{$size} {
          #{$prop}-bottom: $length !important;
        }
        .#{$abbrev}l#{$infix}-#{$size},
        .#{$abbrev}x#{$infix}-#{$size} {
          #{$prop}-left: $length !important;
        }
      }
    }
  }
}
