@use "sass:meta";
@use "../globals";
@use "../mixins/color";
@use "../mixins/text";
@use "../mixins/position";


:root {
  // Font properties
  --default-font-sans-serif: #{meta.inspect(globals.$font-family-sans-serif)};
  --default-font-monospace: #{meta.inspect(globals.$font-family-monospace)};
  --bkkr-font-family: var(--default-font-sans-serif);


  // Background and text color defaults
  --bkkr-background-color: #{globals.$theme-background-color-value};
  --bkkr-background-color-rgb: #{globals.$theme-background-color-rgb-value};
  --bkkr-text-color: #{globals.$theme-text-color-value};
  --bkkr-text-color-rgb: #{globals.$theme-text-color-rgb-value};


  // Gutters
  --bkkr-spacer: #{globals.$spacer-value};
}


// Color classes

@each $color-name, $value in globals.$theme-colors {
  .color-#{$color-name} {
    @include color.classes($color-name);
  }
}


// Safe margins

@supports (padding-top: env(safe-area-inset-top)) {
  :root {
    --safe-area-top: env(safe-area-inset-top);
    --safe-area-bottom: env(safe-area-inset-bottom);
    --safe-area-left: env(safe-area-inset-left);
    --safe-area-right: env(safe-area-inset-right);
  }
}


// Hide content while loading components

html:not(.hydrated) body {
  display: none;
}


// Page Structure

body {
  @include text.font-smoothing();
  background-color: var(--bkkr-background-color);
  color: var(--bkkr-text-color);

  font-family: var(--bkkr-font-family);
}

.bkkr-page {
  @include position.set(0, 0, 0, 0);

  display: flex;
  position: absolute;

  flex-direction: column;
  justify-content: space-between;

  background-color: var(--bkkr-background-color);

  contain: layout size style;
  overflow: hidden;
  z-index: 0;
}

// TODO: Better solution?
.drawer-viewport,
.modal-viewport {
  padding-top: 32px;

  bkkr-content:first-child {
    --padding-top: 0;
    --padding-bottom: calc(var(--bkkr-spacer, globals.$spacer-value) + var(--safe-area-bottom))
  }
}

.split-pane-visible > .bkkr-page.split-pane-main {
  position: relative;
}

bkkr-route,
bkkr-route-redirect,
bkkr-router,
bkkr-select-option,
.bkkr-page-hidden,
[hidden] {
  /* stylelint-disable-next-line declaration-no-important */
  display: none !important;
}

.bkkr-page-invisible {
  opacity: 0;
}