@use "../globals/theme";
@use "../functions/color";

// Generate color classes with adjustable values and fallback color

@mixin classes($color-name) {
  $value: map-get(theme.$colors, $color-name);

  $base: map-get($value, base);
  $contrast: map-get($value, contrast);
  $shade: map-get($value, shade);
  $tint: map-get($value, tint);

  --color-base: var(--color-#{$color-name}, #{$base}) !important;
  --color-base-rgb: var(--color-#{$color-name}-rgb, #{color.color-to-rgb-list($base)}) !important;
  --color-contrast: var(--color-#{$color-name}-contrast, #{$contrast}) !important;
  --color-contrast-rgb: var(--color-#{$color-name}-contrast-rgb, #{color.color-to-rgb-list($contrast)}) !important;
  --color-shade: var(--color-#{$color-name}-shade, #{$shade}) !important;
  --color-shade-rgb: var(--color-#{$color-name}-shade-rgb, #{color.color-to-rgb-list($shade)}) !important;
  --color-tint: var(--color-#{$color-name}-tint, #{$tint}) !important;
  --color-tint-rgb: var(--color-#{$color-name}-tint-rgb, #{color.color-to-rgb-list($tint)}) !important;
}
