@use "../globals";
@use "../mixins/border-radius";

// stylelint-disable declaration-no-important


$directions: top, right, bottom, left !default;


// Border

.border {
  border: globals.$hairlines-width solid rgba(globals.$theme-text-color-rgb, .4) !important;
}

@each $dir in $directions {
  .border-#{$dir} {
    border-#{$dir}: globals.$hairlines-width solid rgba(globals.$theme-text-color-rgb, .4) !important;
  }
}


// No border

.border-0 {
  border: 0 !important;
}

@each $dir in $directions {
  .border-#{$dir}-0 {
    border-#{$dir}: 0 !important;
  }
}


// Rounding

.rounded {
  @include border-radius.set(globals.$radius !important);
}

.rounded-circle {
  @include border-radius.set(50% !important);
}

.rounded-0 {
  @include border-radius.set(0 !important);
}

@each $dir in $directions {
  .rounded-#{$dir} {
    @include border-radius.set(globals.$radius !important, $dir);
  }
}
