@use "../mixins/text";
@use "../mixins/border-radius";
@use "../mixins/state";
@use "../mixins/margin";
@use "../mixins/padding";
@use "../globals";

// stylelint-disable declaration-no-important, selector-list-comma-newline-after


// Typography
//
// _ prefix to make it private
%_heading {
  @include margin.set(0, null, globals.$headings-margin-bottom, null);

  color: globals.$headings-color;

  font-family: globals.$headings-font-family;
  font-style: globals.$headings-font-style;
  font-weight: globals.$headings-font-weight;

  line-height: globals.$headings-line-height;
}

@each $name, $size in globals.$headings {
  #{$name}, .#{$name} {
    @extend %_heading;
    @include text.font-size($size);
  }
}


// Lead

.lead {
  @include text.font-size(globals.$lead-font-size);
  font-weight: globals.$lead-font-weight;
}


// Reset margins on paragraphs
//
// Similarly, the top margin on `<p>`s get reset. However, we also reset the
// bottom margin to use `rem` units instead of `em`.

p {
  @include margin.set(0, null, globals.$paragraph-margin-bottom, null);
}

// Blockquote

blockquote {
  @include margin.set(0, 0, 1rem, null);
}


// Strong
//
// Add the correct font weight in Chrome, Edge, and Safari

b,
strong {
  font-weight: globals.$font-weight-bold;
}


// Small
//
// Add the correct font size in all browsers

small {
  @include text.font-size(globals.$small-font-size);
}


// Mark

mark {
  @include padding.set(globals.$mark-padding);

  background-color: globals.$mark-bg;
}


// Sub and Sup
//
// Prevent `sub` and `sup` elements from affecting the line height in
// all browsers.

sub,
sup {
  @include text.font-size(globals.$sub-sup-font-size);
  position: relative;

  line-height: 0;

  vertical-align: baseline;
}

sub {
 bottom: -.25em;
 }
sup {
 top: -.5em;
 }


// Links

a {
  color: var(--text-color);

  text-decoration: none;
}


// Simple link

a:not([class]) {
  @include state.hover() {
    color: globals.$link-hover-color;

    text-decoration: globals.$link-hover-decoration;
  }
  color: globals.$link-color;

  text-decoration: globals.$link-decoration;
}


// And undo styles for placeholder links/named anchors (without href).
// It would be more straightforward to just use a[href] in previous block, but that
// causes specificity issues in many other styles that are too complex to fix.

a:not([href]):not([class]) {
  &,
  &:hover {
    color: inherit;

    text-decoration: none;
  }
}


// 1. By using `float: left`, the legend will behave like a block element.
//    This way the border of a fieldset wraps around the legend if present.
// 2. Correct the text wrapping in Edge.
// 3. Fix wrapping bug.
//    See https://github.com/twbs/bootstrap/issues/29712

legend {
  @include text.font-size(globals.$legend-font-size);
  @include padding.set(globals.$mark-padding);
  @include margin.set(null, null, globals.$legend-margin-bottom, null);
  width: 100%;

  // stylelint-disable-next-line property-disallowed-list
  float: left; // 1

  font-weight: globals.$legend-font-weight;

  line-height: inherit;

  white-space: normal; // 2

  + * {
    clear: left; // 3
  }
}
