@use "../globals/feature";


// Border radius

@mixin set($radius: globals.$radius, $dir: null, $fallback-border-radius: false) {
  @if feature.$rounded {
    @if $dir == "left" or $dir == "right" {
      border-top-#{$dir}-radius: $radius;
      border-bottom-#{$dir}-radius: $radius;
    }

    @else if $dir == "top" or $dir == "bottom" {
      border-#{$dir}-left-radius: $radius;
      border-#{$dir}-right-radius: $radius;
    }

    @else {
      border-radius: $radius;
    }
  }

  @else if $fallback-border-radius != false {
    @if $dir == "left" or $dir == "right" {
      border-top-#{$dir}-radius: $fallback-border-radius;
      border-bottom-#{$dir}-radius: $fallback-border-radius;
    }

    @else if $dir == "top" or $dir == "bottom" {
      border-#{$dir}-left-radius: $fallback-border-radius;
      border-#{$dir}-right-radius: $fallback-border-radius;
    }

    @else {
      border-radius: $fallback-border-radius;
    }
  }
}
