@use "../globals/code";
@use "../globals/variable";
@use "../mixins/text";
@use "../mixins/border-radius";
@use "../mixins/padding";
@use "../mixins/box-shadow";


// Init

pre,
code,
kbd,
samp {
  font-family: variable.$font-family-code;

  line-height: 1.5;
}


// Inline code

code,
.code {
  /**
   * @prop --background: Background of the badge
   * @prop --color: Text color of the badge
   */
  --background: #{code.$code-background};
  --color: #{code.$code-color};

  @include border-radius.set(code.$code-border-radius);
  @include text.font-size(code.$code-font-size);
  @include padding.set(code.$code-padding-y code.$code-padding-x);

  display: inline-block;

  background: var(--background);
  color: var(--color);

  white-space: pre-wrap;
  word-break: break-word;

  // Streamline the style when inside anchors to avoid broken underline and more
  a > & {
    color: inherit;
  }
}


// User input typically entered via keyboard

kbd,
.kdb {
  @include border-radius.set(2px);
  @include text.font-size(code.$kbd-font-size);
  @include padding.set(code.$kbd-padding-y code.$kbd-padding-x);

  /**
   * No customization available.
   */

  background-color: code.$kbd-background;
  color: code.$kbd-color;

  kbd {
    @include box-shadow.set(none);
    @include padding.set(0);

    font-size: 100%;
    font-weight: code.$kbd-font-weight;
  }
}


// Blocks of code

pre,
.pre {
  @include text.font-size(code.$code-font-size);
  /**
   * No customization available.
   */

  display: block;

  color: code.$pre-color;

  // Account for some code outputs that place code tags in pre tags
  code {
    color: inherit;

    font-size: inherit;
    word-break: normal;
  }
}

// Enable scrollable blocks of code
.pre-scrollable {
  max-height: code.$pre-scrollable-max-height;
  overflow-y: scroll;
}
